body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem;
  font-weight: 300;
  text-align: center;
  color: #FFFFFF;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
}

h4 {
  font-size: 2.6rem;
  margin-top: 1rem;
  margin-left: 2rem;
  text-align: center;
  color: #FFFFFF;
}

h5 {
  font-size: 6rem;
  text-align: center;
  color: #FFFFFF;
  font-family: "Times New Roman", Times, serif;
  font-weight: 300;
  transform: scaleY(1.1);
  margin: 0%;
  margin-top: 7rem;
}

h6 {
  text-align: center;
  font-size: 3rem;
  color: #FFFFFF;
  font-family: "Mr De Haviland", cursive;
  margin: 1rem;
  transform: scaleY(1.4);
  font-weight: 100;
}

.seccion {
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.contenedorPapure {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

@media (min-width: 768px) {
  .contenedorPapure {
    display: flex;
    justify-content: space-around;
    flex-direction: row;
  }

  .contenido {
    display: flex;
    flex-direction: column;
  }
}
.bolita {
  background-color: green;
  height: 4rem;
  width: 4rem;
  align-items: center;
  border-radius: 3rem;
  margin-left: 5rem;
  text-align: center;
  color: white;
  font-weight: 700;
}

.contenido {
  display: flex;
}

.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-amarillo:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-amarillo-block:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-verde {
  background-color: rgba(33, 117, 33, 0.8901960784);
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-verde:hover {
  background-color: rgba(27, 97, 27, 0.8901960784);
  cursor: pointer;
}

.boton-rojo-block {
  background-color: rgb(189, 7, 7);
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  width: 100%;
}
.boton-rojo-block:hover {
  background-color: #a40606;
  cursor: pointer;
}

.boton-verde-block {
  background-color: rgba(11, 105, 11, 0.8235294118);
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  border-radius: 3rem;
  margin-left: 1rem;
  margin-right: 1rem;
  margin-top: 1rem;
  margin-block-end: 1rem;
  font-size: 1.4rem;
}
.boton-verde-block:hover {
  background-color: rgba(9, 82, 9, 0.8235294118);
  cursor: pointer;
}

.dark-mode {
  background-color: black;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #000000;
}
.dark-mode .anuncio,
.dark-mode .resumen-propiedad {
  background-color: #1a1818;
  border: 1px solid #1a1818;
}
.dark-mode .anuncio .precio,
.dark-mode .resumen-propiedad .precio {
  color: rgba(128, 238, 77, 0.8078431373);
}
.dark-mode .anuncio .icono,
.dark-mode .resumen-propiedad .icono {
  filter: invert(100%);
}
.dark-mode .texto-nosotros blockquote {
  color: #FFFFFF;
}

