.contenedorbolita {
  margin-top: 10rem;
  display: flex;
  justify-content: space-around;
  margin-right: 15rem;
}

.vectorXD {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  text-align: center;
}

.motosex {
  width: 250%;
  margin-left: -40rem;
}

.textompapu {
  color: white;
  margin: 10rem;
  font-family: "Lato", sans-serif;
}

.contenedorXD {
  background-color: rgba(128, 238, 77, 0.8078431373);
  border-radius: 6rem;
  margin-right: 7rem;
  margin-left: 7rem;
  margin-top: 7rem;
  padding: 5rem;
  margin-block-end: 8rem;
}

.titulo {
  color: white;
  font-weight: 400;
