}

@media (min-width: 768px) {
  .contenedor-campos {
    display: grid;
    grid-template-columns: 50%, 50%;
    grid-template-rows: auto auto 20rem;
    column-gap: 1rem;
  }

  .campo:nth-child(3),
.campo:nth-child(4) {
    grid-column: 1/3;
  }
}
.campo {
  margin-bottom: 1rem;
}
.campo .pilin {
  width: auto 50rem;
}

.campo label {
  color: var(--blanco);
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
}

.campo textarea {
  height: 10rem;
}

.xd h2 {
  color: white;
}

.w-sm-100 {
  width: 100%;
}

@media (min-width: 768px) {
  .w-sm-100 {
    width: auto;
  }
}
.flex {
  display: flex;
}

.alinear-derecha {
  justify-content: center;
  margin: -23rem;
}

.boton {
  background-color: rgba(33, 117, 33, 0.8901960784);
  color: white;
  padding: 1rem 1rem;
  margin-top: 3rem;
  font-size: 2rem;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold;
  border-radius: 4rem;
