  border: none;
}

@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  cursor: pointer;
}

.header {
  background-color: black;
  padding: 1rem 0 4rem 0;
  padding-top: 4rem;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

.footer {
  padding: 1rem 0 1rem 0;
  background-color: black;
  margin-bottom: 0;
}
.footer .contenido-footer {
  padding-top: 2.5rem;
}
.footer .navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  visibility: visible;
  opacity: 1;
  height: auto;
}
@media (min-width: 768px) {
  .footer .navegacion {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .footer .navegacion legend {
    margin-top: -6rem;
  }
}
.footer .navegacion .carrito {
  width: 4rem;
  margin-top: 3rem;
  margin-bottom: 1rem;
  filter: invert(100%);
}
.footer .navegacion .carrito:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .footer .navegacion .carrito {
    margin-top: 0;
  }
}
.footer .navegacion legend {
  background-color: black;
  border: 1px solid black;
  color: #FFFFFF;
  font-size: 1.8rem;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.izquierda {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .izquierda {
    align-items: flex-end;
  }
}
.izquierda .carrito {
  width: 4rem;
  filter: invert(100%);
}
.izquierda .carrito:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .izquierda .carrito {
    margin-top: 0;
  }
}
.izquierda .dark-mode-boton {
  width: 3rem;
  margin-top: 3rem;
  margin-bottom: 1rem;
  filter: invert(100%);
}
.izquierda .dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .izquierda .dark-mode-boton {
    margin-top: 0;
  }
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha:hover {
  cursor: pointer;
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
  font-weight: 300;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2.6rem;
    font-size: 1.8rem;
    transform: scaleY(1.1);
  }
}
.navegacion a:hover {
  color: rgba(128, 238, 77, 0.8078431373);
}

